.operator {
    background-color: #ea920e;
    color: white;
}



.input {
    height: 3em;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: 700;
    font-size: 1.4em;
    background-color: black;
    color: #FFF;
    padding: 10px;
}
.clear-btn {
    border-radius: 50%;
    margin:5px;
    height: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
